html {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
}

.body {
  margin: 0;
  padding: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  text-align: left;
  margin-top: 4px;
  margin-bottom: 0;
  margin-left: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: flex-end;
  align-items: flex-end;
  gap: 17px;
  max-width: 166px;
  margin-block: 8px;
  display: flex;
}

.card__review-amount {
  text-align: right;
  color: #060b35;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;
  width: 53px;
  height: 16px;
  margin: 6px 0 10px;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
  display: flex;
}

.stars {
  justify-content: start;
  align-items: center;
  gap: 4px;
  margin: 8px 0 12px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  flex-direction: row;
  width: 16px;
  height: 16px;
  display: flex;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__price {
  justify-content: space-between;
  max-width: 166px;
  height: 18px;
  margin: 5px 0 15px;
  display: flex;
  overflow: hidden;
}

.card__price-title {
  color: #616070;
  width: 31px;
  height: 18px;
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-amount {
  text-align: right;
  color: #060b35;
  width: 50px;
  height: 18px;
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
}

.card__buy:hover {
  box-sizing: border-box;
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
  line-height: 39px;
}
